<template>
    <div class="table">
        <div class="box-wrap" v-if="showCategoryBox">
            <!-- 遮罩层 -->
            <div class="box-layer" @click="showCategoryBox=!showCategoryBox"></div>
            <div class="box">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="分类名">
                        <el-input placeholder="请输入分类名" v-model="form.categoryName" clearable></el-input>
                    </el-form-item>

                    <el-form-item label="权重">
                        <el-input placeholder="分组权重(数字越大排序越靠前面)" v-model="form.sortOrder" clearable></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button @click="showCategoryBox=!showCategoryBox">取消</el-button>
                        <el-button type="primary" @click="onSubmit">创建</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-form :inline="true" label-width="80px">
                    <el-button @click="showCategoryAdd" >添加分类</el-button>
                </el-form>
            </div>
            <el-table :data="dataList" border class="table" ref="multipleTable">
                <el-table-column label="名称" min-width="80" prop="categoryName"></el-table-column>

                <el-table-column label="操作时间" min-width="80">
                    <template slot-scope="scope">
                        <p v-if="scope.row.createTime">创建时间:{{scope.row.createTime}}</p>
                        <p v-if="scope.row.delTime">删除时间:{{scope.row.delTime}}</p>
                    </template>
                </el-table-column>

                <el-table-column label="操作" min-width="80">
                    <template slot-scope="scope">
                        <!-- <el-button
                            type="text"
                            icon="el-icon-sort-up"
                            @click="handleGoods('up',scope.row)"
                        >升序</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-sort-down"
                            @click="handleGoods('down',scope.row)"
                        >降序</el-button>-->
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            @click="handleGoods('del', scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
export default {
    name: "categoryList",
    data() {
        return {
            dataList: [], //列表数据
            
            parames: {
                pageNo: 1,
                pageSize: 100
            }, //请求参数
            showCategoryBox:false,
            //添加分类参数
            form: {
                sortOrder: 0,
                categoryName: ""
            },
        };
    },
    created() {
        this.getDataList();
    },
    methods: {
        async getDataList() {
            const data = await this.$api.CategoryList(this.parames);
            this.dataList = data;
        },
        //添加分类
        async addCategory(categoryName) {
            const data = await this.$api.CategoryAdd(this.form);
            this.showCategoryBox=false
            this.getDataList();
        },
        //显示添加分类
        showCategoryAdd() {
            this.form={
                sortOrder: 0,
                categoryName: ""
            }
            this.showCategoryBox=true
        },
        onSubmit(){
            if(this.form.categoryName==""){
                this.$message.console.error('分类名不能为空');
                return
            }
            this.addCategory()
        },
        //分类处理
        handleGoods(status, row) {
            this.$confirm("删除该分类, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    this.categoryDel(row.categoryId);
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
        },
        //分类删除
        async categoryDel(categoryId) {
            const data = await this.$api.CategoryDelete({ categoryId });
            this.getDataList();
        }
    }
};
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.table {
    width: 100%;
    font-size: 14px;
}

.box-wrap {
    position: relative;
    width: 100%;
    height: 100%;
    top: 0;
}

/*遮罩层样式*/
.box-layer {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
}

/*不限制图片大小，实现居中*/
.box {
    padding: 30px;
    width: 400px;
    border-radius: 10px;
    background: #fff;
    position: absolute;
    left: 0;
    right: 0;
    margin: 50px auto;
    z-index: 1000;
}
</style>
